.table {
  margin-top: 20px;
  overflow: scroll;
  height: 400px;
  color: #6a5d5d;
  overflow-x: hidden;
  /* background-color: white; */
}
.table tr{
    display: flex;
    justify-content: space-between;
}

.table td {
    padding: 0.7rem;
}

.table tr:nth-of-type(odd){
    background-color: #f3f2f8;
}