.app_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

body {
  background-color: #f5f6fa;
}

.app{
    display: flex;
    justify-content: space-evenly;
    padding: 30px;
    
}

.app_left{
   flex: 0.7;
   padding-right: 12px;
}
.app_right{
    flex: 0.3;
    
}

.app_stats {
  display: flex;
  justify-content: space-between;

}

@media (max-width:990px){
    .app{
        flex-direction: column;
    }
}

@media (max-width:490px){
    .app{
      flex-direction: column;
        
    }
}



